* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  color: #fff;
  outline: none;
}

a {
  text-decoration: none;
}

html {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 100%;
  scroll-behavior: smooth;
  overflow: hidden;
}

body {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  overflow: hidden;
}

img {
  width: 100%;
  height: auto;
}
